@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
/* Start Navbar Style */
.navbar {
    width: 216px;
    box-shadow: 9px 0px 25px -11px #ccccccb5;
    height: 100vh;
}
.navbar .logo {
    margin: 38px 0 50px 14px;
}
.navbar .logo img.inst-text {
    position: relative;
    top: 4px;
    margin-left: 15px;
}
.navbar .nav-items {
    margin-top: 50px;
}
.navbar .nav-items .nav-item {
    list-style-type: none;
    padding: 12px 26px;
    border-left: 5px solid #fff;
    margin-bottom: 4px;
}
.navbar .nav-items .nav-item i {
    position: relative;
}
.navbar .nav-items .nav-item i.fa-satellite-dish::after {
    content: "\f144";
    font-family: "Font Awesome 5 Free";
    font-weight: 100;
    color: rgb(247 62 96 / 78%);
    font-size: 10px;
    position: absolute;
    top: -4px;
    left: 16px;
}
.navbar .nav-items .nav-item i.fa-comment-alt::after {
    content: "8";
    color: #ffffff;
    font-size: 10px;
    position: absolute;
    top: -6px;
    left: 18px;
    background-color: rgb(247 62 96 / 78%);
    padding: 0 3px;
    border-radius: 50%;
}
.navbar .nav-items .nav-item i.fa-bell::after {
    content: "5";
    color: #ffffff;
    font-size: 10px;
    position: absolute;
    top: -3px;
    left: 10px;
    background-color: rgb(247 62 96 / 78%);
    padding: 0 3px;
    border-radius: 50%;
}
.navbar .nav-items .nav-item.active, .navbar .nav-items .nav-item:hover {
    background-color: #F9F9F9;
    border-color: #F73E60;
}
.navbar .nav-items .nav-item i {
    font-size: 24px;
    margin-right: 16px;
    text-shadow: 1px 0px 4px rgb(123 123 123 / 50%);
}
.navbar .nav-items .nav-item a {
    text-decoration: none;
    color: #9A9999;
    font-size: 14px;
    display: flex;
    align-items: center;
}
/* End Navbar Style */
.main {
    width: calc(100% - 216px);
}
/* Start Header Style */
header i {
    color: #F73E60;
    font-size: 30px;
}
header {
    padding: 14px 0;
    box-shadow: 0px 9px 25px -19px #f73e6047;
}
header .header-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
}
header .search input {
    border: none;
    background-color: #F5F5F5;
    height: 34px;
    width: 330px;
    border-radius: 30px;
    padding: 0px 24px;
    box-sizing: border-box;
}
header .search {
    position: relative;
}
header .search input::-webkit-input-placeholder {
    color: #d3d3d3;
}
header .search input:-ms-input-placeholder {
    color: #d3d3d3;
}
header .search input::-ms-input-placeholder {
    color: #d3d3d3;
}
header .search input::placeholder {
    color: #d3d3d3;
}
header .search i {
    font-size: 14px;
    position: absolute;
    top: 9px;
    right: 16px;
}
.profile-section {
    display: flex;
    align-items: center;
}
.profile-section .profile-pic {
    width: 52px;
    height: 52px;
    border-radius: 50%;
    overflow: hidden;
}
.profile-section .profile-pic img {
    width: 100%;
}
.profile-section .profile-info {
    margin-left: 16px;
}
.profile-section .profile-info .profile-name {
    color: #F73E60;
    margin: 0 0 3px;
    font-size: 12px;
}
.profile-section .profile-info span {
    color: #d8d8d8;
    display: block;
    font-size: 10px;
}
.profile-section .profile-info span:first-of-type{
    margin-bottom: 1px;
}
/* End Header Style */
/* Start Stories Style */
.stories {
    display: flex;
    align-items: center;
    padding: 25px 12px 0 42px;
}
.stories .add-icon {
    background-color: #F73E60;
    width: 65px;
    height: 65px;
    border-radius: 50%;
    overflow: hidden;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}
.stories .add-icon i {
    border: 1px dashed #fff;
    font-size: 10px;
    padding: 4px 4px;
}
.stories .add-story {
    text-align: center;
    margin-right: 18px;
}
.stories .add-story span {
    font-size: 11px;
    color: #9a9999;
}
.stories .stories-items {
    display: flex;
    justify-content: space-around;
}
.stories .stories-items .circle-side {
    position: absolute;
    top: -2px;
}
.stories .stories-items .circle-side span {
    color: #817F7F;
    font-size: 12px;
    margin-left: 14px;
}
.stories .stories-items .circle-img {
    width: 72px;
    height: 72px;
    border-radius: 50%;
    overflow: hidden;
    border: 3px solid #fff;
    box-sizing: border-box;
}
.stories .stories-items .circle-img img {
    width: 100%;
}
.stories .stories-items .story {
    position: relative;
    margin: 7px;
}
.stories .stories-items .rectangle-side {
    z-index: -1;
    text-align: right;
}
.stories .stories-items .rectangle-side span {
    color: #ccc;
    font-size: 12px;
}
.stories .stories-items .rectangle-side .rectangle-img {
    /* width: 26%; */
    /* height: 82px; */
    overflow: hidden;
}
.stories .stories-items .rectangle-side .rectangle-img img {
    width: 100%;
    border-top-left-radius: 60px;
    border-bottom-left-radius: 60px;
}
/* End Stories Style */
/* Start Posts Style */
.posts {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-gap: 25px;
    padding: 42px 70px;
}
.posts .post {
    box-shadow: 0px 4px 7px -2px #cccccc;
    overflow: hidden;
}
.posts .post-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 4px 8px;
}
.posts .post-top .person-left {
    display: flex;
    align-items: center;
}
.posts .post-top .info-tab i {
    color: #F73E60;
}
.posts .post-top .person-img {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    overflow: hidden;
    border: 2px solid #f73e60;
}
.posts .post-top .person-img img {
    width: 100%
}
.posts .person-info {
    display: flex;
    flex-direction: column;
    margin-left: 12px;
}
.posts .person-info h5 {
    margin: 0;
    font-size: 10px;
    color: #6d6d6d;
    font-weight: 600;
    margin-bottom: 2px;
}
.posts .person-info span {
    font-size: 8px;
    color: #cccccc;
}
.posts .post-img img {
    width: 100%;
}

.posts .post-bottom {
    display: flex;
    justify-content: space-between;
    padding: 6px 15px 8px;
}
.posts .post-bottom .left-icons i {
    padding-right: 12px;
}
.posts .post-bottom i {
    color: #ccc;
}
.posts .post-bottom .left-icons i.fa-heart {
    color: #f73e60
}
/* Start Posts Style */
body {
    font-family: 'Roboto', sans-serif;
    margin: 0;
}
ul {
    margin: 0;
    padding: 0;
}
.wrapper {
    max-width: 1039px;
    margin: auto;
    display: flex;
    outline: 1px solid #ddd;
}
.main {
    width: calc(100% - 216px);
}
